@import 'utils/variables';
@import 'components/type';

// fonts install
@font-face {
    font-family: 'Header font';
    src: url(../assets/fonts/ringbearer-font/RingbearerMedium-51mgZ.ttf);
}

@font-face {
    font-family: 'forum';
    src: url(../assets/fonts/Forum/Forum-Regular.ttf);
}

@font-face {
    font-family: 'work sans';
    src: url(../assets/fonts/Work_Sans/WorkSans-VariableFont_wght.ttf);
}

body{
    background-image: url(../assets/images/mobile/cre패dits_bg.png);
}

@media only screen and (min-width: 700px){
    body{
        background-image: url(../assets/images/tab/cre패dits_bg.png);
    }
}

@media only screen and (min-width: 1200px){
    body{
        background-image: url(../assets/images/cre패dits_bg.png);
    }
}

a{
    background-image: url(../assets/images/cre패dits_nuage.png);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
    width: 30vw;
    height: 20vw;
    max-width: 200px;
    max-height: 130px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $fontColor;

    &:hover{
        text-decoration: underline;
        cursor: pointer;
    }
    &:active{
        font-weight: bold;
    }

    p{
        margin: 0;
    }
}