body, html{
  height: 100%;
}

body{
  padding: 0;
  margin: 0;
  color: $fontColor;
  text-shadow: 0px 0px 5px black;
  width: 100vw;
  height: 100vh;
  overflow: scroll;
  background-attachment: fixed;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
}

main{
  width: 70vw;
  margin: 0 auto;
}

p{
  line-height: $lineHeight;
  font-family: 'work sans';
  font-size: $fs-mobile-p;
  margin: 0;
  margin-bottom: $mt-18px;
}

a{
  line-height: $lineHeight;
  font-family: 'work sans';
  font-size: $fs-mobile-p;
  margin: 0;
  text-decoration: none;
  margin-bottom: $mt-72px;
  margin-top: $mt-72px;
}

h1{
  line-height: $lineHeight;
  font-family: 'Header font';
  font-size: $fs-mobile-h1;
  margin-top: 0;
  margin-bottom: $mt-72px;
}

h2{
  line-height: $lineHeight;
  font-family: 'forum';
  font-size: $fs-mobile-h2;
  margin: 0;
  margin-bottom: $mt-51px;
}

h3{
  line-height: $lineHeight;
  font-family: 'forum';
  font-size: $fs-mobile-h3;
  margin-bottom: $mt-25px;
}

ul{
  padding: 0;
  margin: 0;
  margin-bottom: $mt-51px;
}

@media only screen and (min-width: 700px) {
  p{
    font-size: $fs-p;
    margin-bottom: $mt-20px;
  }
  
  a{
    font-size: $fs-p;
    margin-bottom: $mt-80px;
    margin-top: $mt-80px;
  }
  
  h1{
    font-size: $fs-h1;
    margin-bottom: $mt-80px;
  }
  
  h2{
    font-size: $fs-h2;
    margin-bottom: $mt-56px;
  }
  
  h3{
    font-size: $fs-h3;
    margin-bottom: $mt-28px;
  }
  
  ul{
    margin-bottom: $mt-56px;
  }
}

